import Vue from 'vue'
import App from './App.vue'
import './common/reset.css'
import axios from 'axios'
import _ from 'lodash'
import router from './router'

Vue.config.productionTip = false
Vue.prototype.$axios = axios
Vue.prototype._ = _


new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
