<template>
  <div>
    <div class="options">
      <div id="ployline-option">
        是否清除轨迹线:
        <label> <input type="radio" name="clear" value="1" v-model="clearPrePolyline"/>是 </label>
        <label> <input type="radio" name="clear" value="0" v-model="clearPrePolyline"/>否 </label>
        <button id="clear" @click="clearAllPolyline">清除所有轨迹</button>
        <span>1.点击红色小圆点可显示病例轨迹</span>
        <span>2.先隐藏一个功能才能点击下一个功能</span>
      </div>

      <button id="landMark" type="button" @click="showLandMark">
        显示地标
      </button>
      <button id="timeline" type="button" @click="showTimeLine">
        显示疫情扩散
      </button>
      <button id="outside" type="button" @click="showOutside">
        显示外传病例
      </button>
    </div>

    <div class="text">{{landMarkInfo}}</div>
  </div>
</template>

<script>
export default {
  name: 'Options',
  props:{
    landMarkInfo: {
      type:String,
      default:''
    }
  },
  data() {
    return {
      clearPrePolyline:1
    }
  },
  methods: {
    showLandMark (e) {
      this.$emit('showLandMark',e.target.innerText)
      e.target.innerText = e.target.innerText === '显示地标' ? '隐藏地标' : '显示地标'
    },
    showTimeLine (e) {
      this.$emit('showTimeLine',e.target.innerText)
      e.target.innerText = e.target.innerText === '显示疫情扩散' ? '隐藏疫情扩散' : '显示疫情扩散'
    },
    showOutside (e) {
      this.$emit('showOutside',e.target.innerText)
      e.target.innerText = e.target.innerText === '显示外传病例' ? '隐藏外传病例' : '显示外传病例'
    },
    clearAllPolyline() {
      this.$emit('clearAllPolyline')
    }
  },
  watch: {
    clearPrePolyline(val) {
      this.$emit('clearPrePolyline',val)
    }
  }
}
</script>

<style lang="stylus" scoped>
.options
  position absolute
  z-index 999
  top 7vh
  left 10px
  #ployline-option
    border-radius 5px
    background-color #fff
    padding 5px 7px
    box-shadow 0 3px 5px rgba(0, 0, 0, 0.1)
    line-height 20px
    #clear
      margin-left 5px
      outline none
      border none
      border-radius 5px
      background-color #fff
      padding 3px 7px
      box-shadow 0 2px 5px rgba(0, 0, 0, 0.2)
      transition 0.3s
      &:hover
        box-shadow 0 2px 5px rgba(0, 0, 0, 0.5)
    span
      display block
      font-size 14px
      font-weight bold
  #landMark, #timeline, #outside
    margin 10px 5px
    border-radius 5px
    background-color #fff
    padding 5px 7px
    box-shadow 0 2px 5px rgba(0, 0, 0, 0.2)
    transition 0.3s
    &:hover
      box-shadow 0 3px 5px rgba(0, 0, 0, 0.5)
.text
  position absolute
  bottom 1vh
  left 10px
  z-index 999
  border-radius 5px
  background-color #fff
  padding 8px 12px
  box-shadow 0 3px 5px rgba(0, 0, 0, 0.2)
</style>