<template>
  <div>
    <div id="allmap"></div>
    <Options
      @showLandMark="showLandMark"
      @showTimeLine="showTimeLine"
      @showOutside="showOutside"
      @clearAllPolyline="clearAllPolyline"
      @clearPrePolyline="clearPrePolyline"
      :landMarkInfo="landMarkInfo"
    ></Options>
  </div>
</template>

<script>
import AMap from 'AMap'
import AMapUI from 'AMapUI'
import Options from '../components/Options'
export default {
  name: 'Covid',
  components: {
    Options,
  },
  data() {
    return {
      amap: null,
      gd_points: [], //高德坐标点
      polylines: [], //折线数组
      all_paths: [],
      landmarkers: [], //地标标记点
      landmarks: null, //地标数据
      timelines: null, //时间线数据
      each_day_markers: [], //每日数据
      outside: [],
      outside_paths: [],
      every_points: [],
      dates: [], //日期列表
      heatmap: null, //热力图对象
      gd_paths: [], //高德路径
      gd_each_days: [],
      timer: null, //计时器对象
      pathSimplifierIns: null,
      landMarkInfo: '鼠标移入展示地标信息',
      clearPrePolylineNum: '1',
    }
  },
  async created() {
    await this.getOutside()
    await this.getLandMarkAndShow()
    await this.getTimeline()
    await this.getDalian()
    this.amap = new AMap.Map('allmap', {
      resizeEnable: true,
      zoom: 11,
      center: [121.62, 38.92],
      mapStyle: 'amap://styles/whitesmoke',
    })
    this.showCircleAndHeatmap()
  },
  mounted() {},
  methods: {
    showLandMark(text) {
      if (text === '显示地标') {
        this.amap.add(this.landmarkers)
      } else {
        this.amap.remove(this.landmarkers)
        this.landMarkInfo = '鼠标移入展示地标信息'
      }
    },
    showTimeLine(text) {
      let that = this
      if (text === '显示疫情扩散') {
        this.initMap()
        this.heatmap.setMap(this.amap)
        let options = {
          radius: 40,
          blur: 0.7, //值越小内圆越大
          opacity: [0, 0.9],
          gradient: {
            // 除以最大值
            0.05: 'blue',
            0.25: 'rgb(117,211,248)',
            0.5: 'rgb(0, 255, 0)',
            1.0: '#ffea00',
            // 1.0: 'red'
          },
        }
        this.heatmap.setOptions(options)
        let index = 0
        if (this.timer) {
          clearInterval(this.timer)
          this.timer = null
        }
        this.timer = setInterval(() => {
          if (index < that.gd_each_days.length) {
            that.pushData(that.gd_each_days, index)
            that.amap.add(that.each_day_markers) //添加m每日地点
            that.landMarkInfo = `${that.dates[index]}` //显示日期
            let heatmapData = []
            let gdCount = that._.countBy(that.every_points)
            for (let k in gdCount) {
              let lng = k.split(',')[0]
              let lat = k.split(',')[1]
              let count = gdCount[k]
              heatmapData.push({
                lng,
                lat,
                count,
              })
            }
            that.heatmap.setDataSet({
              data: heatmapData,
              max: 30,
            })
            index++
          } else {
            that.landMarkInfo = '结束'
          }
        }, 1000)
      } else {
        clearInterval(that.timer)
        that.timer = null
        that.setMap()
        that.landMarkInfo = '鼠标移入展示地标信息'
      }
    },
    showOutside(text) {
      let that = this
      if (text === '显示外传病例') {
        this.amap.clearMap()
        this.heatmap.setMap(null)
        this.amap.setZoom(7)
        AMapUI.load(['ui/misc/PathSimplifier'], function (PathSimplifier) {
          if (!PathSimplifier.supportCanvas) {
            alert('当前环境不支持 Canvas！')
            return
          }
          //启动页面
          that.initOutsideIns(PathSimplifier)
        })
      } else {
        if (that.pathSimplifierIns) {
          that.pathSimplifierIns.setData([])
        }
        that.pathSimplifierIns.clearPathNavigators()
        that.amap.setCenter([121.62, 38.92])
        that.amap.setZoom(11)
        that.setMap()
      }
    },
    clearAllPolyline() {
      this.amap.remove(this.polylines)
      this.polylines = []
    },
    clearPrePolyline(val) {
      this.clearPrePolylineNum = val
    },

    // 随机生成一种颜色
    getColor() {
      return '#' + Math.floor(Math.random() * 16777215).toString(16)
    },

    // 添加路径
    addPolyline(path) {
      let polyline = new AMap.Polyline({
        path: path,
        strokeWeight: 6, // 线条宽度，默认为 1
        strokeColor: this.getColor(), // 线条颜色
        // strokeOpacity: 0.6, //线条透明度
        lineJoin: 'round', // 折线拐点连接处样式,
        showDir: true, //路径是否展示白色箭头
      })
      this.polylines.push(polyline)
      this.amap.add(polyline)
    },

    // 热力图配置
    showheatmap(data, max = 30) {
      let that = this
      that.amap.plugin(['AMap.HeatMap'], function () {
        that.heatmap = new AMap.HeatMap(that.amap, {
          zIndex: 100, //设置index，修复setInterval时地图闪屏
          radius: 40,
          blur: 0.7, //值越小内圆越大
          opacity: [0, 0.9],
          gradient: {
            0.05: 'blue',
            0.25: 'rgb(117,211,248)',
            0.5: 'rgb(0, 255, 0)',
            1.0: '#ffea00',
            // 1.0: 'red'
          },
        })
        that.heatmap.setDataSet({
          data: data,
          max: max,
        })
      })
    },
    // 添加地标标记点
    addCircle(point, markers, extData = '') {
      let that = this
      let circle = new AMap.CircleMarker({
        center: point,
        zIndex: 999,
        radius: 5,
        strokeColor: '#2980b9',
        strokeOpacity: 0.8,
        strokeWeight: 1,
        fillColor: '#3498db',
        fillOpacity: 0.5,
        extData: extData,
      })
      if (extData !== '') {
        circle.on('mouseover', function () {
          that.landMarkInfo = this.getExtData()
        })
      }
      markers.push(circle)
    },
    addCircleAndEvent(point) {
      let that = this
      let circle = new AMap.CircleMarker({
        center: point, //设置线覆盖物路径
        // zIndex: 999,
        radius: 5,
        strokeColor: '#c42027', //边框线颜色
        strokeOpacity: 0.8, //边框线透明度
        strokeWeight: 1, //边框线宽
        fillColor: '#e86444', //"#e86444", //填充色
        fillOpacity: 0.3, //填充透明度,
      })
      circle.on('click', function () {
        let paths = that.gd_paths.filter((path) => {
          return that._.find(path, this.getCenter())
        })
        if (that.clearPrePolylineNum === '1') {
          that.amap.remove(that.polylines)
        }
        paths.forEach((path) => {
          that.addPolyline(path)
        })
      })
      that.amap.add(circle)
    },

    // 地图覆盖物内容展示
    showCircleAndHeatmap() {
      let that = this
      that.gd_paths.forEach((path) => {
        path.forEach((point) => {
          that.addCircleAndEvent(point)
          // 整理出热力图数据
          let { lng, lat } = point
          let lnglat = '' + lng + ',' + lat
          that.gd_points.push(lnglat)
        })
      })
      let heatmapData = []
      let gdCount = that._.countBy(this.gd_points)
      for (let k in gdCount) {
        let lng = k.split(',')[0]
        let lat = k.split(',')[1]
        let count = gdCount[k]
        heatmapData.push({
          lng,
          lat,
          count,
        })
      }
      that.showheatmap(heatmapData)
    },
    pushData(data, index) {
      let that = this
      data[index].forEach((point) => {
        that.addCircle(point, that.each_day_markers)
        let { lng, lat } = point
        let lnglat = '' + lng + ',' + lat
        that.every_points.push(lnglat)
      })
    },

    // 初始化所有数据
    initMap() {
      // 初始化数据
      this.gd_points = []
      // this.landmarkers = []
      this.each_day_markers = []
      this.every_points = []
      this.timer = null
      // heatmap = null
      // 初始化地图
      if (!this.amap) {
        this.amap = new AMap.Map('allmap', {
          resizeEnable: true,
          zoom: 11,
          center: [121.62, 38.92],
          mapStyle: 'amap://styles/whitesmoke',
        })
      } else {
        // 如果地图不为空，清除所有覆盖物和热力图
        this.amap.clearMap()
        this.heatmap.setMap(null)
        this.landMarkInfo = '鼠标移入展示地标信息'
      }
    },
    setMap() {
      this.initMap()
      this.showCircleAndHeatmap()
    },
    // 路径初始化
    initOutsideIns(PathSimplifier) {
      //创建组件实例
      this.pathSimplifierIns = new PathSimplifier({
        zIndex: 100,
        map: this.amap, //所属的地图实例
        getPath: function (pathData) {
          //返回轨迹数据中的节点坐标信息，[AMap.LngLat, AMap.LngLat...] 或者 [[lng|number,lat|number],...]
          return pathData.path
        },
        getHoverTitle: function (pathData, pathIndex, pointIndex) {
          //返回鼠标悬停时显示的信息
          if (pointIndex >= 0) {
            //鼠标悬停在某个轨迹节点上
            return (
              pathData.name + '，点:' + pointIndex + '/' + pathData.path.length
            )
          }
          //鼠标悬停在节点之间的连线上
          return pathData.name + '，点数量' + pathData.path.length
        },
        renderOptions: {
          //轨迹线的样式
          pathLineStyle: {
            strokeStyle: 'red',
            lineWidth: 6,
            dirArrowStyle: true,
          },
        },
      })
      //这里构建两条简单的轨迹，仅作示例
      this.pathSimplifierIns.setData(this.outside_paths)
      //创建一个巡航器
      for (let i = 0; i < this.outside_paths.length; i++) {
        let len = this.outside[i].length
        var navg = this.pathSimplifierIns.createPathNavigator(
          i, //关联第1条轨迹
          {
            loop: true, //循环播放
            speed: 10000 * len,
          }
        )
        navg.start()
      }
    },
    async getOutside() {
      let response = await this.$axios.get('json/waichuan.json')
      let data = response.data
      let outside = [],
        outside_paths = []
      let citys = Object.keys(data)
      let values = Object.values(data)
      for (let i = 0; i < values.length; i++) {
        let temp = Object.values(values[i]).flat()
        outside.push(temp)
      }
      for (let i = 0; i < outside.length; i++) {
        let path = outside[i].map((item) => {
          let lnglat = item.split(',').map((i) => parseFloat(i))
          return lnglat
        })
        outside_paths.push({
          name: citys[i],
          path: path,
        })
      }
      this.outside = outside
      this.outside_paths = outside_paths
    },
    async getLandMarkAndShow() {
      let response = await this.$axios.get('json/landmark.json')
      this.landmarks = response.data
      for (let k in this.landmarks) {
        let [lng, lat] = this.landmarks[k].split(',')
        let point = new AMap.LngLat(lng, lat)
        this.addCircle(point, this.landmarkers, k)
      }
    },
    async getTimeline() {
      let response = await this.$axios.get('json/timeline.json')
      let data = response.data
      let gd_each_days = []
      this.dates = Object.keys(data)
      for (let day in data) {
        let gd_each_day = data[day].map((item) => {
          let [lng, lat] = item.split(',')
          return new AMap.LngLat(lng, lat)
        })
        gd_each_days.push(gd_each_day)
      }
      this.gd_each_days = gd_each_days
    },
    async getDalian() {
      let response = await this.$axios.get('json/dalian.json')
      let data = response.data
      let all_paths = [],
        gd_paths = []
      for (let i in data) {
        for (let j in data[i]) {
          all_paths.push(data[i][j])
        }
      }
      all_paths.forEach((path) => {
        let gd_path = path.map((item) => {
          let [lng, lat] = item.split(',')
          return new AMap.LngLat(lng, lat)
        })
        gd_paths.push(gd_path)
      })
      this.all_paths = all_paths
      this.gd_paths = gd_paths
    },
  },
}
</script>

<style lang="stylus" scoped>
#allmap
  width 100vw
  height 95vh
</style>