import Vue from 'vue'
import VueRouter from 'vue-router'
import Covid from '../pages/Covid.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Covid',
    component: Covid
  },
  {
    path: '/cityinfo',
    name: 'CityInfo',
    component: () => import('../pages/CityInfo.vue')
  },
  {
    path: '/migrate',
    name: 'Migrate',
    component: () => import('../pages/Migrate.vue')
  },
  {
    path: '/movein',
    name: 'MoveIn',
    component: () => import('../pages/MoveIn.vue')
  },
  {
    path: '/willmovein',
    name: 'WillMoveIn',
    component: () => import('../pages/WillMoveIn.vue')
  },
]

const router = new VueRouter({
  routes
})

export default router
